        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
    }
    
    .Assignment_Logo{
        margin-left: auto;
        margin-right: auto;
        vertical-align: middle;
        width: 50px;
    }

    .Assignment_Text{
        display: grid;
        grid-template-rows: 6fr 4fr;
        padding-right: 10px;
    }

